import Link from "next/link";
import styles from "./linkPage.module.css";
import { useState, useEffect } from "react";

type Props = {
  children: React.ReactNode;
  url: string;
};

export const LinkPage = ({ children, url }: Props) => {
  const [isActive, setIsActive] = useState(false); // Флаг активности ссылки

  const currentHref = window.location.href;
  console.log(currentHref);
  useEffect(() => {
    if (currentHref.includes(url)) {
      setIsActive(true);
    } else {
      setIsActive(false);
    }
  }, [isActive]);

  const handleClick = () => {
    setIsActive(true); // Устанавливаем флаг активности при клике
  };

  const activeClass = isActive ? styles.linkActive : styles.link; // Определяем активный класс

  return (
    <Link href={url} className={activeClass} onClick={handleClick}>
      {children}
    </Link>
  );
};
